<template>

	<div class="CycleCount">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>盘点</span><i class="iconfont icon-saoma" @click="scan"></i></header>

		<div class="container">

			<div>
				<span>盘点单号</span>
				<input ref="CountNo"  @change="toUppercase('CountNo')"  placeholder="请输入内容" v-model="CountNo"  @focus="g_focus('CountNo')" @keyup.enter="skipLoc"></input>
				<i v-if="CountNo"  @click="clearInput('CountNo')"  class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="choiceNo" ></i>
			</div>

			<div>
				<span>盘点类型</span>
				<select v-model="CountType" class="CountType" @change="CountTypeSelected">
					<option :value="type.id" v-for="type in CountTypeList">{{type.name}}</option>  
				</select>
			</div>

			<div>
				<span>盘点库位</span>
				<input ref="Loc"  placeholder="请输入内容"   @change="toUppercase('Loc')"  v-model="Loc"  @keyup.enter="confirmOnclick"  @focus="g_focus('Loc')"  @blur="g_blur()"></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>

			<span @click="confirmOnclick" class="g_singleBtn">确认</span>
		</div>

		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">

	export default{
		name:"CycleCount",
		data(){
			return{		
				CountNo:'', //测试数据 1234567
				CountTypeList:
				[{
					id: 'PSku',
					name: '商品'
				},
				{
					id: 'PSkuxLottable',
					name: '商品、批号'
				},
				{
					id: 'PSkuxLot',
					name: '商品、系统批号'
				}],
				CountType:'',
				Loc:'',  //6010120
				tipsHide:false, //提示信息是否显示
				msg:'',     //提示信息别人
				inputFocus:''
			}
		},
		mounted() {

			this.$refs.Loc.focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){
			this.CountType = this.CountTypeList[0].id;

			if(this.$route.query.SelectOrder){

				this.CountNo = this.$route.query.SelectOrder.CountNo;
				this.CountType = this.$route.query.SelectOrder.CountType;

			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				if(inputName == "CountNo"){

					this.getFocus('Loc');

				}else if(inputName == "Loc") {

					this.confirmOnclick();
				}
			},
			CountTypeSelected(){
				console.log(this.CountType);
			},
			// 跳到 盘点库位
			skipLoc(){

				this.$refs['Loc'].focus();
			},
			// 跳转到 选择订单 页面
			choiceNo(){
				var that = this;

				var currentUrl = this.HOST + "RFCountSel.do";

				var postData = {
					"CountNo": this.CountNo,
				}

				this.httpRequest(currentUrl,postData,function(res){

					that.$store.state.CycleCount_RFCountSel = res.data.data;

					that.$router.push({
						name:"CycleCount_ChoiceNo",
						query:{

						}
					})
				},"CountNo")
			},
			confirmOnclick(){
				
				var that = this;

				if(!this.CountNo){

					this.msg = "盘点单号不能为空";
					that.tipsShow("CountNo");
					that.inputFocus = "CountNo";
				}else{
					if(!this.Loc){

						this.msg = "盘点库位不能为空";
						that.tipsShow("Loc");
						that.inputFocus = "Loc";
					}else{

						var currentUrl = this.HOST + "RFCountQry.do";

						var postData = {
							"CountNo":this.CountNo,
							"CountType":this.CountType,
							"Loc":this.Loc,
						}

						this.httpRequest(currentUrl,postData,function(res){

							that.$router.push({
								name:"CycleCount_Submit",
								query:{
									CountNo:that.CountNo,
									CountType:that.CountType,
									Loc:that.Loc
								}
							})
							that.$store.state.CycleCount_RFCountQry = res.data.data;
						},"CountNo")
					}
				}
			},
			//返回上一页  首页
			toIndex:function(){

				this.$router.push({
					name:"Index",
				})
			},
		}
	}
</script>
<style scoped lang="less">
.CycleCount{
	.container{
		padding-top: 1/50rem;
		&>div{
			width: 100%;
			height: 70/50rem;
			margin-top: 20/50rem;
			box-sizing: border-box;
			position: relative;
			span{
				width: 140/50rem;
				text-align: right;
				float: left;
				height: 55/50rem;
				line-height: 55/50rem;
			}

			input::placeholder{
				font-size: 25/50rem;
			}

			input{
				margin-left: 10/50rem;
				width: 510/50rem;
				height: 55/50rem;
				padding: 0 10/50rem;
				box-sizing: border-box;
			}
			i{
				position:absolute;
				right:120/50rem;
				font-size: 30/50rem;
				top: 13/50rem;
			}
			i.icon-sousuo{
				right:25/50rem;
				font-size: 40/50rem;
				top: 8/50rem;
			}
			.CountType{
				margin-left: 10/50rem;
				width: 511/50rem;
				height: 62/50rem;
				border-radius: 5/50rem;
				border: 1/50rem solid #aaa;
				padding: 0 10/50rem;
				font-size: 30/50rem;
			}
		}
		
		&>div:nth-child(3){
			margin-bottom: 50/50rem;
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){
	
	.CycleCount{
		.container{
			&>div{
				span{
					width: 150/50rem;
				}
				.CountType{
					width: 510/50rem;
					border: 3/50rem solid #aaa;
				}
			}
		}
	}
}

// 240像素）
@media screen and (width: 240px){
	
	.CycleCount{
		.container{
			&>div{
				height: 100/50rem;
				span{
					width: 200/50rem;
					height: 80/50rem;
					line-height: 80/50rem;
				}
				
				input{
					width: 450/50rem;
					height: 80/50rem;
				}
				i{	
					font-size: 50/50rem;
					top: 15/50rem;
					right:120/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					top: 5/50rem;
					font-size: 60/50rem;
				}
				.CountType{
					width: 450/50rem;
					height: 80/50rem;
					border: 5/50rem solid #aaa;
				}
			}
		}
	}
}

</style>


